.landing__top {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-end;
}

.landing__bottom {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.landing__header {
    font-size: 125px;
    color: #f8f8f8;
    font-weight: 650;
}

.landing__nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.landing__nav-item {
    font-size: 30px;
    padding: 10px 30px;
}

.landing__nav-item:hover {
    cursor: pointer;
    color: rgba(14, 144, 101);
}

.landing__bottom-down:hover {
    cursor: pointer;
    color: rgba(14, 144, 101);
}